/**
 * root/seminar/client/src/components/FilePreview.jsx
 * FilePreview component for the seminar website.
 */

export default function FilePreview({ file }) {
  if (!file) {
    return <div></div>;
  }

  if (file.endsWith(".pdf")) {
    return (
      <iframe
        src={`/files/${file}`}
        className="pdf-frame"
        title="PDF Preview"
      ></iframe>
    );
  }

  if (file.endsWith(".md")) {
    return <div>Markdown: {file}</div>;
  }

  return <div>: {file}</div>;
}
